import { BASE_IMAGE_URL,getGoodsList } from '@/utils/api'

import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			list: [1,2,3,4,5,6,7,8,9,0,11,22,33,44],
			loadingType: 0,
			page: 1,
			typeList: ['综合', '最新', '价格', '销量'],
			curType: 0,
			g_type: 0
		}
	},
	methods: {
		changeType(index){
			this.curType = index;
		},
		async getList(){
			let res = await getGoodsList(this.g_type,this.page);
			uni.stopPullDownRefresh();
			if(res.code == 1000){
				if(this.page == 1){
					this.list = res.data;
				}else{
					this.list = this.list.concat(res.data);
				}
				
				if(res.data.length < 10){
					this.loadingType = 2;
				}else{
					this.loadingType = 0;
				}
			}
		},
		goToDetail(){
			uni.navigateTo({
				url: '/pages/express_detail/express_detail'
			});
		}
	},
	onReachBottom() {
		this.loadingType = 1;
		this.page++;
		this.getList();
	},
	
	onPullDownRefresh() {
		console.log("dddddd");
		this.page = 1;
		this.list = [];
		this.getList();
	},
	onLoad(options){
		this.g_type = options.g_type;
		this.getList();
	}
	
}
